@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap');

*{
  font-family: Poppins;
  padding:0;


}


@keyframes mymove {
  from {background-color: #3E5641;}
  to {background-color: #282B28;}
}
body {
  padding-top: 15px;
  background: white;
  animation: mymove 6s infinite alternate;
  margin: auto;
  padding:25px;
}
.header{
  display:flex;
  align-items: center;
  flex-flow: column;
  text-align: center;
}
.info
{
  color:White;
  font-weight: 200;
  font-size:0.9rem;

}
.b{
  display:flex;
  color:white;
  justify-content: space-around;
  gap:20px;
}
.best{
font-weight: 400;
padding-top:10px;
}
.p{
  font-weight: 200;
}
.btn{
  background-color: black;
  color:white;
  font-size:1rem;
  font-weight: 200;
  border:none;
  padding:5px;
  margin-top:15px;
  width:100%
}
img{
  width:auto; 
  height:145px;
}
@media only screen and (max-width: 600px) {
 img{
  width:auto; 
  height:60px;
 }
}

#root{
  display:flex;
  flex-flow: column;
  align-content: center;
  align-items: center;
}
.end{
  display: flex;
  align-items: center;
  align-content: center;
  margin:auto;
  margin-top:35vh;
  justify-content: center;
}
.logo{
  font-weight: 700;
  color:white;
  font-size: 1.6rem;
}
.cont{
  display: flex;
  flex-flow: wrap;
  gap:15px;
  justify-content: center;
  align-items: center;
  margin-top:20px;
}
img{
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.name{
  font-weight: 200;
  font-size:0.9rem;
}
.seconds{
  background-color: white;
  padding:3px;
  display:flex;
  align-items: center;
  justify-content: center;
  margin-top:-7px;
  border-end-start-radius: 5px;
  border-end-end-radius: 5px;
}